}
@media screen and (max-width: 990px) {
  .steps__item {
    --_image-position--x: 80%;
    --_image-size--width: 320px;
    --_image-position--y: -15rem;
  }
}
@media screen and (max-width: 767px) {
  .steps__item {
    --_image-position--x: 50%;
    --_image-size--width: 300px;
    --_image-position--y: -18rem;
    --_transform--tramslateX: -50%;
  }
  .steps__item img {
    left: var(--_image-position--x) !important;
