}
.steps__item img {
  position: absolute;
  top: var(--_image-position--y);
  left: var(--_image-position--x);
  width: var(--_image-size--width);
  aspect-ratio: var(--_image-aspect-ratio);
  transform: translateX(var(--_transform--tramslateX));
  height: unset;
}
.steps__item:nth-child(even) {
  margin-left: auto;
}
.steps__item:nth-child(even) img {
  left: unset;
  right: var(--_image-position--x);
}
.steps__item:nth-child(2) {
